.control-panel {
     height: 40px;
    display: flex;
    justify-content: center;
    margin: 0 10px;
    line-height: 40px;
}

.control-panel > button {
        padding: 10px 20px;
        margin: 0 10px;
        font-size: 18px;
        text-align: center;
        cursor: pointer;
        outline: none;
        color: #fff;
        background-image: linear-gradient(black,white);
        border: none;
        border-radius: 15px;
        box-shadow: 0 9px #aaa;
}
.save-btn  .load-btn{
    background-image: linear-gradient(red,white);
}

.control-panel > button:active {
    background-color: #3e8e41;
    box-shadow: 0 5px #666;
    transform: translateY(4px);
}

.sliders{
    display: flex;
    flex-direction: row;
    position: absolute;
    left: 3%;
    top: 70%;
    text-align: center;
    line-height: 20px;
    height: 100px;
    width: 20px;
    transform: rotate(270deg);

}

.slider {
    margin: 0 20px;
    display: flex;
    flex-direction: row;
    padding: 20px 0;

}

.slider > * {
    padding: 20px 0;
}

.rotate-back {
    transform: rotate(-270deg);
    font-weight: bold;
}

.current-tempo {
    width: 50px;
    color: white;
    padding: 5px;
    margin-top: 10px;
    border: 2px solid white;
    background-color: rgba(255, 0, 0, 0.3);
    border-radius: 25px;
    font-size: 1.5em;
    line-height: 32px;
    cursor: default;
}

.bpm {
    color: rgba(255, 125, 0, 0.8);
    text-shadow: 2px 2px rgba(0, 0, 0, 0.5);
    font-size: 1.5em;
    padding: 0;
    cursor: default;
}

#volume-icon {
    cursor: pointer;
    padding: 0;
    width: 30px;
    height: 30px;
    line-height: 30px;
    text-align: center;
    margin: 13px 2px;
    position: relative;

}

.mute {

    position: absolute;
    width: 2px;
    height: 1px;
    top: 28%;
    left: 325px;
    z-index: 1;
    background-color: red;
    transform: rotate(45deg)
    
}

.hidden {
    display: none;
}